.flip-card {
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  -webkit-transition: -webkit-transform 0.8s;
  transition: -webkit-transform 0.8s;
  transition: transform 0.8s;
  transition: transform 0.8s, -webkit-transform 0.8s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.flip-card:hover .flip-card-inner {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.flip-card-back {
  text-align: left;
  background-color: dodgerblue;
  background-color: rgb(24, 49, 72);
  /* background-color: #224054; */
  color: white;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  padding: 30px;
}

.flip-card-face {
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0px 0px 200px 0px rgba(255, 255, 255, 0.5);
}
.bubble {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    z-index: 100;
    margin-left: -100px;
    margin-top: -100px;
    position: fixed;
    box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2), inset 0px 10px 30px 5px rgba(255, 255, 255, 1);
    /*background: radial-gradient(ellipse at center, rgba(0,180,255, 0) 0%,rgba(255,255,255,0.5) 70%);*/
    pointer-events: none;
}

.bubble:after {
    background: radial-gradient(ellipse at center, rgba(0, 180, 255, 0.45) 0%, rgba(255, 255, 255, 0) 70%);
    box-shadow: inset 0 20px 30px rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    content: "";
    height: 180px;
    left: 10px;
    position: fixed;
    width: 180px;
}
